<template>
    <!-- <Mycircle :diam=300 :n=100></Mycircle>
    <Mycircle :diam=250 :n=30></Mycircle>
    <Mycircle :diam=200 :n=7></Mycircle> -->
    <Mycircle :diam=350 :Items=years.items :On_i=years.On_i></Mycircle>
    <Mycircle :diam=300 :Items=months.items :On_i=months.On_i></Mycircle>
    <Mycircle :diam=250 :Items=date.items :On_i=date.On_i></Mycircle>
    <Mycircle :diam=200 :Items=days.items :On_i=days.On_i></Mycircle>

</template>
<script>
import Mycircle from './Mycircle.vue'
export default {
    data(){
        return{
            years : {items: [],On_i : Number},
            months : {items: [],On_i: Number},
            date : {items: [],On_i: Number},            
            days : {items: ["월","화","수","목","금","토","일"],On_i: Number} ,
            // day :[],
            // months :['1월','2월','3월','4월','5월','6월','7월','8월','9월','10월','11월','12월'],
            // days : ["월","화","수","목","금","토","일"],
        }
    },
    mounted() {
        
        this.setThisday();
    },
    methods : {
        getYears(birth){
            // for(var i=birth;i<birth+100;i+=3)
            var currentTime = new Date();
            var cnt = 0;
            for(var i=birth;i<birth+100;i+=2 )
            {
                this.years.items.push(i+'년');
                if(i==currentTime.getFullYear())
                   this.years.On_i = cnt;

                   cnt ++;
                   }
            },
        getDate(){
            for(var i=1;i<=31;i++)
            this.date.items.push(i+'일');
            },
        getMonths(){
            for(var i=1;i<=12;i++)
                this.months.items.push(i+'월');
            },
        setThisday(birth = 1992)
            {
                this.getYears(birth);
                this.getMonths();
                this.getDate();

                var currentTime = new Date();
                this.months.On_i= currentTime.getMonth();
                this.date.On_i= currentTime.getDate();
                this.days.On_i= currentTime.getDay();
            }
  },
  components: { Mycircle },
    
}
</script>
